
import { postPageData } from '@/service/main/system/system'
import { useStore } from '@/store'
import { Notify } from 'vant'
import { computed, defineComponent, ref } from 'vue'

export default defineComponent({
  setup() {
    const title = ref('')
    const message = ref('')
    const store = useStore()
    const frequency = ref(0)
    const userId = computed(() => store.state.login.userInfo.identity_number)

    setInterval(() => (frequency.value = 0), 1000 * 3600)
    const handleBtnClick = async () => {
      if (frequency.value >= 5) {
        Notify({
          type: 'warning',
          message: 'Please give feedback from time to time'
        })
        return
      }
      if (!userId.value) {
        Notify({
          type: 'warning',
          message: 'Please login first'
        })
        return
      }

      if (title.value.length >= 50) {
        Notify({
          type: 'warning',
          message: 'Title is to long...'
        })
        return
      }

      const feedbackResult = await postPageData('/feedback/add', {
        user_id: userId.value,
        title: title.value,
        message: message.value
      })

      if (feedbackResult.code === 200) {
        Notify({
          type: 'success',
          message: 'Thanks for your feedback!'
        })
      }
      frequency.value++
    }

    return {
      title,
      message,
      handleBtnClick
    }
  }
})
